span.glyphicon-remove {
    color: red;
}

$table_contexts: (
    success: (
        color: "#a94442",
        background-color: "#f2dede",
        border: "1px solid #ebccd1"
    ),
    danger: (
        color:"#3c763d",
        background-color: "#dff0d8",
        border: "1px solid #d6e9c6"
    )
);

@mixin renderTableContextStyles($settings) {
    @each $context, $styles in $settings {
        .table {
            & > tbody {
                & > tr {
                    & > td {
                        & > div.#{$context} {
                            color: #3c763d;
                            background-color: #dff0d8;
                            border: 1px solid #d6e9c6;
                        }
                    }
                }
                
                & > tr.#{$context} {
                    & > td, & > th {
                        color: #3c763d;
                        background-color: #dff0d8;
                        border: 1px solid #d6e9c6;
                    }
                }
            }
        }
    }
}

